.jq-checkbox { top: -1px; width: 13px; height: 13px; border: 1px solid #C3C3C3; border-radius: 3px; background: #F6F6F6; box-shadow: 0 0 1px 0px rgba(0, 0, 0, 0.05); vertical-align: middle; cursor: pointer; }

.jq-checkbox.checked .jq-checkbox__div { width: 9px; height: 9px; margin: 2px 0 0 2px; background: #666; box-shadow: 0 0 1px 0px rgba(0, 0, 0, 0.05); }

.jq-checkbox.disabled { opacity: 0.5; }

.jq-radio { width: 12px; height: 12px; border: 1px solid #C3C3C3; border-radius: 50%; background: #F6F6F6; box-shadow: 0 0 0 0px transparent; vertical-align: middle; cursor: pointer; }

.jq-radio.checked .jq-radio__div { width: 6px; height: 6px; margin: 3px 0 0 3px; background: #666; }

.jq-radio.disabled { opacity: 0.5; }

.jq-file { width: 270px; box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1); border-radius: 4px; }

.jq-file input { cursor: pointer; height: auto; line-height: 1em; }

.jq-file__name { -moz-box-sizing: border-box; box-sizing: border-box; width: 100%; height: 32px; padding: 0 80px 0 10px; color: #333; font: 14px/30px Arial, sans-serif; white-space: nowrap; text-overflow: ellipsis; overflow: hidden; border: 1px solid #CCC; border-bottom-color: #B3B3B3; border-radius: 4px; box-shadow: inset 1px 1px #F1F1F1; background: #FFF; }

.focused .jq-file__name { border: 1px solid #5794BF; }

.jq-file__browse { position: absolute; top: 1px; right: 1px; padding: 0 10px; border-left: 1px solid #CCC; border-radius: 0 4px 4px 0; background: #F1F1F1 url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAAeCAIAAABi9+OQAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAABdJREFUeNpi+v//PxM2zMDAQEtxgAADAF2uTe9L4getAAAAAElFTkSuQmCC) repeat-x; box-shadow: inset 1px -1px #F1F1F1, inset -1px 0 #F1F1F1, 0 1px 2px rgba(0, 0, 0, 0.1); color: #333; text-shadow: 1px 1px #FFF; font: 14px/30px Arial, sans-serif; }

.jq-file:hover .jq-file__browse { background-color: #E6E6E6; background-position: 0 -10px; }

.jq-file:active .jq-file__browse { background: #F5F5F5; box-shadow: inset 1px 1px 3px #DDD; }

.jq-file.disabled .jq-file__name { color: #888; }

.jq-file.disabled, .jq-file.disabled .jq-file__browse { border-color: #CCC; background: #F5F5F5; box-shadow: none; color: #888; }

.jq-selectbox { vertical-align: middle; cursor: pointer; width: 100%; }

.jq-selectbox__select { height: 5rem; padding: 10px 1.5rem 0; border: 1px solid #e5e5e5; border-radius: 0.5rem; background: #fff; box-shadow: 0 0 2px 0px transparent; font-size: 2rem; line-height: 2rem; color: #1c1c1c; font-family: "Roboto", sans-serif; }

.jq-selectbox__select:hover { background: #fff; }

.jq-selectbox__select:active { background: #fff; }

.jq-selectbox.focused .jq-selectbox__select { background: #fff; }

.opened .jq-selectbox__select { border-color: #e5e5e5; }

.jq-selectbox.disabled .jq-selectbox__select { border-color: #CCC; background: #F5F5F5; box-shadow: none; color: #888; }

.jq-selectbox__select-text { display: block; width: 100%; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

.jq-selectbox__trigger { position: absolute; top: 0; right: 0; width: 34px; height: 100%; }

.jq-selectbox__trigger-arrow { position: absolute; top: 14px; right: 12px; width: 0; height: 0; overflow: hidden; border-top: 5px solid #000; border-right: 5px solid transparent; border-left: 5px solid transparent; background: none; }

.jq-selectbox__dropdown { top: 5rem; width: 100%; -moz-box-sizing: border-box; box-sizing: border-box; margin: 0; padding: 0; border: 1px solid #000; border-radius: 4px; background: #FFF; box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.1); -moz-box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.1); -webkit-box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.1); }

.jq-selectbox__search { margin: 5px; }

.jq-selectbox__search input { -moz-box-sizing: border-box; box-sizing: border-box; width: 100%; margin: 0; padding: 5px 27px 6px 8px; outline: none; border: 1px solid #CCC; border-radius: 3px; background: url(data:image/png;base64,) no-repeat 100% 50%; box-shadow: inset 1px 1px #F1F1F1; color: #333; -webkit-appearance: textfield; }

.jq-selectbox__search input::-webkit-search-cancel-button, .jq-selectbox__search input::-webkit-search-decoration { -webkit-appearance: none; }

.jq-selectbox__not-found { margin: 5px; padding: 5px 8px 6px; background: #F0F0F0; font-size: 13px; }

.jq-selectbox ul { margin: 0; padding: 0; }

.jq-selectbox li { min-height: 18px; padding: 5px 10px 5px 10px; color: #1c1c1c; font-size: 2rem; line-height: 2rem; font-family: "Roboto", sans-serif; }

.jq-selectbox li.selected { background-color: #fafafa; color: inherit; }

.jq-selectbox li:hover { background-color: #fafafa; color: inherit; }

.jq-selectbox li.disabled { color: #ccc; background: none; }

.jq-selectbox li.disabled:hover { background: none; }

.jq-selectbox li.optgroup { font-weight: bold; }

.jq-selectbox li.optgroup:hover { background: none; color: #231F20; cursor: default; }

.jq-selectbox li.option { padding-left: 25px; }

.jq-select-multiple { -moz-box-sizing: border-box; box-sizing: border-box; padding: 1px; border: 1px solid #CCC; border-bottom-color: #B3B3B3; border-radius: 4px; box-shadow: inset 1px 1px #F1F1F1, 0 1px 2px rgba(0, 0, 0, 0.1); background: #FFF; color: #333; font: 14px/18px Arial, sans-serif; cursor: default; }

.jq-select-multiple.focused { border: 1px solid #5794BF; }

.jq-select-multiple.disabled { border-color: #CCC; background: #F5F5F5; box-shadow: none; color: #888; }

.jq-select-multiple ul { margin: 0; padding: 0; }

.jq-select-multiple li { padding: 3px 9px 4px; list-style: none; }

.jq-select-multiple li:first-child { border-radius: 3px 3px 0 0; }

.jq-select-multiple li:last-child { border-radius: 0 0 3px 3px; }

.jq-select-multiple li.selected { background: #08C; color: #FFF; }

.jq-select-multiple li.disabled { color: #AAA; }

.jq-select-multiple.disabled li.selected, .jq-select-multiple li.selected.disabled { background: #CCC; color: #FFF; }

.jq-select-multiple li.optgroup { font-weight: bold; }

.jq-select-multiple li.option { padding-left: 25px; }

html, body { height: 100%; }

html { font-size: 62.5%; }

body { color: #1c1c1c; font-family: "Roboto", sans-serif; font-size: 1.4rem; line-height: 2.4rem; }

h1 { color: #000; font-size: 7rem; font-weight: 100; margin-bottom: 6rem; text-align: center; }

h2 { color: #1c1c1c; font-size: 3rem; font-weight: 300; margin-bottom: 4.5rem; margin-top: 2rem; text-align: center; }

button, select, label[for], input[type='submit'], input[type='image'] { color: inherit; cursor: pointer; outline: none !important; }

input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button { -webkit-appearance: none; margin: 0; }

input[type='number'] { -moz-appearance: textfield; }

input::-ms-clear { display: none; }

:disabled, [disabled], [aria-disabled="true"] { cursor: not-allowed; }

a { color: inherit; text-decoration: none; }

a:hover { text-decoration: none; }

strong { font-weight: 700; }

b { font-weight: 600; }

input, select, button, textarea { font: inherit; outline: none; }

img { height: auto; max-width: 100%; }

[class^="col-"] { flex-basis: auto; }

/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header { background: #fff; border-bottom: 1px solid #d9d9d9; height: 8rem; font-weight: 300; min-width: 320px; width: 100%; }

header nav { height: 100%; }

header .nav { height: 100%; }

header .nav .nav-item { align-items: center; display: flex; text-transform: uppercase; }

header .nav .nav-item:hover a, header .nav .nav-item.active a { color: #637ab4; }

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content { background-image: linear-gradient(transparent 100px, #fff 195px), url("../img/main-bg.jpg"); background-size: 100% 100%, auto 200px; background-repeat: no-repeat; background-position: top center; height: 1000px; min-width: 320px; padding-top: 15rem; }

#personal-info { margin-bottom: 4rem; }

#experience { margin-bottom: 6rem; }

.form-control { border-color: #e5e5e5; border-radius: .5rem; font-size: 2rem; font-weight: 400; resize: none; }

.form-control::-webkit-input-placeholder { color: #c8c8c8; }

.form-control:-moz-placeholder { color: #c8c8c8; }

.form-control::-moz-placeholder { color: #c8c8c8; }

.form-control:-ms-input-placeholder { color: #c8c8c8; }

.form-control:focus { border-color: #e5e5e5; box-shadow: none; }

textarea { margin: 4rem auto; }

.form-check .form-check-input { display: none; }

.form-check .form-check-input:checked + .form-check-label:before { background: #435993; content: ""; }

.form-check .form-check-label { padding-left: 3.2rem; position: relative; }

.form-check .form-check-label:hover:before { background: #e9efff; }

.form-check .form-check-label:before { background: #fff; border: 1px solid #435993; border-radius: .25rem; color: #fff; content: ''; display: block; font-family: 'forgrant' !important; font-size: 8px; height: 2rem; line-height: 2rem; left: 0; position: absolute; text-align: center; top: .3rem; width: 2rem; }

.form-group:not(.form-check) { margin: auto; max-width: 520px; width: 100%; }

.form-group:not(.form-check) input[type="text"], .form-group:not(.form-check) input[type="email"] { height: 5rem; text-align: center; }

.form-group:not(.form-check) .styled-select { margin-bottom: 2rem; max-width: 320px; text-align: center; }

.form-group:not(.form-check) .styled-select .jq-selectbox__select { font-weight: 400; height: 100%; line-height: 5rem; padding-top: 0; }

.form-group:not(.form-check) .styled-select .jq-selectbox__trigger { align-items: center; display: flex; justify-content: center; }

.form-group:not(.form-check) .styled-select .jq-selectbox__trigger:before { color: #435993; content: ""; font-family: 'forgrant' !important; font-size: 8px; transform: rotate(180deg); }

.form-group:not(.form-check) .styled-select .jq-selectbox__trigger-arrow { display: none; }

.form-group:not(.form-check) .styled-select .jq-selectbox__dropdown { border: none; box-shadow: 0px 7px 40px -5px rgba(0, 0, 0, 0.4); overflow: auto; top: 5rem; }

.form-group:not(.form-check) .styled-select .jq-selectbox__dropdown li { color: #435993; font-size: 2rem; }

.form-group:not(.form-check) .styled-select .jq-selectbox__dropdown li:first-child { display: none; }

.form-group:not(.form-check) .styled-select.opened .jq-selectbox__trigger:before { transform: none; }

.form-group:not(.form-check) label { font-size: 1.4rem; transition: all 0.3s linear; }

@media screen and (prefers-reduced-motion: reduce) { .form-group:not(.form-check) label { transition: none; } }

.form-group:not(.form-check) .form-control:placeholder-shown + label { font-size: 2rem; opacity: 0; transform: translateY(-3rem); visibility: hidden; }

.form-group:not(.form-check) .form-control:not(:focus) + label { opacity: 0; visibility: hidden; }

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) { .form-group:not(.form-check) label { visibility: hidden; } }

@supports (display: -ms-grid) { .form-group:not(.form-check) label { visibility: hidden; } }

.btn { background: #435993; border: none; font-weight: 300; color: #fff; font-size: 1.6rem; padding: 0 3rem; border-radius: 1.2rem; height: 5rem; margin-bottom: 2rem; }

.btn:hover { background-color: #334470; }
